.backdrop{
    width: 100%;
    height: 100%;
    padding-top: 99px;
    padding-bottom: 100px;
    background:
    -webkit-gradient(
        linear, 
        left top, left bottom, 
        from(rgb(0, 0, 0)), 
        color-stop(rgba(75, 75, 75, 0.468)),
        to(rgb(0, 0, 0))
      ), url('./NotreDame.jpg');
    background:
    -webkit-linear-gradient(
        rgb(0, 0, 0), 
        rgba(75, 75, 75, 0.468),
        rgb(0, 0, 0)
      ), url('./NotreDame.jpg');
    background:
    -o-linear-gradient(
        rgb(0, 0, 0), 
        rgba(75, 75, 75, 0.468),
        rgb(0, 0, 0)
      ), url('./NotreDame.jpg');
    background:
    linear-gradient(
        rgb(0, 0, 0), 
        rgba(75, 75, 75, 0.468),
        rgb(0, 0, 0)
      ), url('./NotreDame.jpg');
    background-size: 100%; 
    background-repeat: no-repeat;
}

@media screen and (max-width: 900px) and (min-width: 100px) {
    .backdrop{
      width: 100%;
      height: 100%;
    }
  }




